import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'footer-app',
    template: require('./footer.component.html'),
    styles: [require('./footer.component.scss')]
})

export class FooterComponent implements OnInit{
    public data: string;

    constructor(){
        this.data = new Date().toLocaleDateString();
    }

    ngOnInit(){}
}


